import PropTypes from "prop-types"
import React from "react"
import {Navbar} from "react-bootstrap"

const Header = ({ siteTitle }) => (
  <header>
    <Navbar>
      <Navbar.Brand href="/">{siteTitle}</Navbar.Brand>
    </Navbar>    
  </header>
)

Header.propTypes = {
  siteTitle: PropTypes.string,
}

Header.defaultProps = {
  siteTitle: ``,
}

export default Header
